import axios from 'axios';
import {useState} from 'react';
import {useMutation} from 'react-query';
import {useHistory} from 'react-router-dom';
import {setToken} from '../../lib/token';
import {Routes} from '../../app/routes';

interface LoginApi {
  readonly login: (credential: Credential) => void;
  readonly signup: (signupIfo: SignupInfo) => void;
  readonly errors: Array<AuthError>;
}

export interface AuthError {
  readonly field: 'email' | 'password' | 'policy';
  readonly message: string;
}

interface Credential {
  readonly email: string,
  readonly password: string
}

interface SignupInfo {
  credential: Credential;
  isPolicyChecked: boolean;
  isRememberChecked: boolean;
}

interface LoginModel {
  readonly accessToken: string;
  readonly refreshToken: string;
}

export const useLogin = (): LoginApi => {
  const history = useHistory();
  const [errors, setErrors] = useState<Array<AuthError>>([]);
  const loginMutation = useMutation<LoginModel, Error, Credential>(loginRequest, {
    onError: error => {
      setErrors([{field: 'email', message: error.message}]);
    },
  });

  const signupMutation = useMutation<LoginModel, Error, Credential>(signupRequest, {
    onError: error => {
      setErrors([{field: 'email', message: error.message}]);
    },
  });

  const tryLogin = (credential: Credential): void => {
    setErrors([]);
    const validationErrors = validateCredential(credential);
    if (validationErrors.length === 0) {
      loginMutation.mutate(credential, {
        onSuccess: (loginData) => {
          setToken(loginData.accessToken);
          history.push(Routes.Home);
        }
      });
    } else {
      setErrors(validationErrors);
    }
  };

  const trySignup = (signupIfo: SignupInfo): void => {
    setErrors([]);
    const validationErrors = validateCredential(signupIfo.credential);

    if (!signupIfo.isPolicyChecked) {
      validationErrors.push({field: 'policy', message: 'Read and agree with privacy policy before sign in'});
    }

    if (validationErrors.length === 0) {
      signupMutation.mutate(signupIfo.credential, {
        onSuccess: (loginData) => {
          setToken(loginData.accessToken, signupIfo.isRememberChecked);
          history.push(Routes.Home);
        },
      });
    } else {
      setErrors(validationErrors);
    }
  };

  return {
    login: tryLogin,
    signup: trySignup,
    errors,
  };
};

const validateCredential = (credential: Credential): Array<AuthError> => {
  const errors: Array<AuthError> = [];

  if (credential.email.length === 0) {
    errors.push({field: 'email', message: 'Please enter email'});
  } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(credential.email)) {
    errors.push({field: 'email', message: 'Please enter a valid email address'});
  }

  if (credential.password.length < 8) {
    errors.push({
      field: 'password',
      message: 'Password must contain at least 8 characters, including numbers, uppercase, and special characters'
    });
  }

  return errors;
};

const loginRequest = (credential: Credential) =>
  axios.get('/login', {
    headers: {
      'Authorization': `Basic ${btoa(`${credential.email}:${credential.password}`)}`
    }
  })
    .then(({data}) => loginModelMapper(data))
    .catch(error => {
      throw error.response.data;
    });

const signupRequest = (credential: Credential) =>
  axios.get('/signup', {
    headers: {
      'Authorization': `Basic ${btoa(`${credential.email}:${credential.password}`)}`
    }
  })
    .then(({data}) => loginModelMapper(data))
    .catch(error => {
      throw error.response.data;
    });

const loginModelMapper = (data: any): LoginModel => ({
  accessToken: data['access-token'],
  refreshToken: data['refresh-token'],
});
