import React, {ChangeEvent, FC, useCallback, useState} from 'react';
import {Routes} from '../../app/routes';
import {Title} from '../../common/Title.component';
import {Screen} from '../../common/Screen.component';
import {Button} from '../../common/Button.component';
import {Checkbox} from '../../common/Checkbox.component';
import {Link} from '../../common/Link.component';
import {Auth} from './Auth.component';
import {useLogin} from './Login.api';
import {ReactComponent as LoginIcon} from './images/login-icon.svg';
import styles from './Signup.module.scss';

export const Signup: FC = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [isPolicyChecked, setIsPolicyChecked] = useState(false);
  const [isRememberChecked, setIsRememberChecked] = useState(false);
  const {signup, errors} = useLogin();

  const handleChangeEmail = (email: string): void => setEmail(email);
  const handleChangePassword = (password: string): void => setPassword(password);

  const handleSignup = useCallback(() => {
    signup({credential: {email, password}, isPolicyChecked, isRememberChecked});
  }, [email, isPolicyChecked, isRememberChecked, password, signup]);

  return (
    <Screen>
      <Screen.Header>
        <Title title="Sign Up" icon={<LoginIcon/>}/>
      </Screen.Header>
      <Screen.Content>
        <Auth
          errors={errors}
          email={email}
          onEmailChange={handleChangeEmail}
          password={password}
          onPasswordChange={handleChangePassword}
        />
        <Link to={Routes.Login}>Already have an account?</Link>
        <div className={styles.additionalInfo}>
          <Checkbox
            checked={isPolicyChecked}
            onChange={(event: ChangeEvent<HTMLInputElement>) => setIsPolicyChecked(event.target.checked)}
            name="policy"
            isValid={!errors.find(({field}) => field === 'policy')}
            additionalClasses={styles.info}
          >
            Agree with <a href="https://questmission.com/privacy-policy">Privacy Policy</a>
          </Checkbox>
          <Checkbox
            checked={isRememberChecked}
            onChange={(event: ChangeEvent<HTMLInputElement>) => setIsRememberChecked(event.target.checked)}
            name="remember"
            additionalClasses={styles.info}
          >
            Remember me on this device
          </Checkbox>
        </div>
      </Screen.Content>
      <Screen.Footer>
        <Button title="Sign Up" onClick={handleSignup}/>
      </Screen.Footer>
    </Screen>
  );
};
