import Cookies from 'js-cookie';
import CryptoJS from 'crypto-js';

const TOKEN_KEY = 'token';
const DAYS_TO_STORE_TOKEN = 3;

export const setToken = (token: string, shouldStore: boolean = false): void => {
  const encryptedToken = CryptoJS.AES.encrypt(token, TOKEN_KEY).toString();
  if (shouldStore) {
    Cookies.set(
      TOKEN_KEY, encryptedToken,
      {expires: DAYS_TO_STORE_TOKEN}
    );
  } else {
    window.sessionStorage.setItem(TOKEN_KEY, encryptedToken);
  }
};

export const getToken = (): string | null => {
  let token: string | undefined | null = Cookies.get(TOKEN_KEY);

  if (!token) {
    token = window.sessionStorage.getItem(TOKEN_KEY);
  }

  return token ? CryptoJS.AES.decrypt(token, TOKEN_KEY).toString() : null;
};
