import React, {ChangeEvent, ChangeEventHandler, FC, useEffect, useState} from 'react';
import classNames from 'classnames';
import styles from './TextInput.module.scss';

interface TextInputProps {
  value?: string;
  onChange?: ChangeEventHandler<HTMLInputElement>;
  type?: 'text' | 'password';
  placeholder?: string;
  label?: string;
  name?: string;
  isValid?: boolean;
  additionalClasses?: string;
}

export const TextInput: FC<TextInputProps> = ({
  type = 'text',
  value,
  onChange,
  placeholder,
  label,
  name,
  isValid = true,
  additionalClasses,
}) => {
  const [hasErrors, setHasErrors] = useState(!isValid);

  useEffect(() => setHasErrors(!isValid), [isValid]);

  const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
    !!onChange && onChange(event);
    setHasErrors(false);
  };

  return (
    <>
      {label && (
        <label className={styles.label} htmlFor={`id_${name}`}>{label}</label>
      )}
      <input
        className={classNames(
          styles.textInput,
          additionalClasses,
          {
            [styles.hasErrors]: hasErrors,
          },
        )}
        value={value}
        onChange={handleChange}
        type={type}
        placeholder={placeholder}
        id={`id_${name}`}
      />
    </>
  );
};
