import React, {ChangeEvent, ChangeEventHandler, FC, useEffect, useState} from 'react';
import classNames from 'classnames';
import styles from './Checkbox.module.scss';

interface CheckboxProps {
  name?: string;
  checked?: boolean;
  onChange?: ChangeEventHandler<HTMLInputElement>;
  isValid?: boolean;
  additionalClasses?: string;
}

export const Checkbox: FC<CheckboxProps> = ({
  children,
  name,
  checked,
  onChange,
  isValid = true,
  additionalClasses,
}) => {
  const [hasErrors, setHasErrors] = useState(!isValid);

  useEffect(() => setHasErrors(!isValid), [isValid]);

  const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
    !!onChange && onChange(event);
    setHasErrors(false);
  };

  return (
    <label
      className={classNames(styles.checkboxContainer, additionalClasses)}
      htmlFor={`id_${name}`}
    >
      <input
        className={styles.nativeCheckbox}
        type="checkbox"
        id={`id_${name}`}
        name={name}
        checked={checked}
        onChange={handleChange}
      />
      <span
        className={classNames(styles.checkbox, {
          [styles.hasErrors]: hasErrors,
        })}
      />
      <span className={styles.label}>{children}</span>
    </label>
  );
};
