import React, {FC, useCallback, useState} from 'react';
import {Routes} from '../../app/routes';
import {Link} from '../../common/Link.component';
import {Title} from '../../common/Title.component';
import {Button} from '../../common/Button.component';
import {Screen} from '../../common/Screen.component';
import {ReactComponent as LoginIcon} from './images/login-icon.svg';
import {useLogin} from './Login.api';
import {Auth} from './Auth.component';
import styles from './Login.module.scss';

export const Login: FC = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const {login, errors} = useLogin();

  const handleChangeEmail = (email: string): void => setEmail(email);
  const handleChangePassword = (password: string): void => setPassword(password);

  const handleLogin = useCallback(() => {
    login({email, password});
  }, [email, login, password]);

  return (
    <Screen>
      <Screen.Header>
        <Title title="Log In" icon={<LoginIcon/>}/>
      </Screen.Header>
      <Screen.Content>
        <Auth
          errors={errors}
          email={email}
          onEmailChange={handleChangeEmail}
          password={password}
          onPasswordChange={handleChangePassword}
        />
        <p className={styles.text}>
          If you forgot your password please contact admin@questmission.com
        </p>
        <Link to={Routes.Signup}>Don&apos;t have an account yet?</Link>
      </Screen.Content>
      <Screen.Footer>
        <Button title="Login" onClick={handleLogin}/>
      </Screen.Footer>
    </Screen>
  );
};
